let medical = require('../assets/objects/medical')
let household = require('../assets/objects/household')
let legal = require('../assets/objects/legal')
let entertainment = require('../assets/objects/entertainment')
let financial = require('../assets/objects/financial')

export function getTypeData (type) {
  if (type === 'medical') {
    return medical
  } else if (type === 'household') {
    return household
  } else if (type === 'legal') {
    return legal
  } else if (type === 'entertainment') {
    return entertainment
  } else {
    return financial
  }
}
