<template>
  <v-layout fill-height align-center justify-center mt-5 pt-5>
    <v-flex xs10 sm7 md4>
      <v-card class="rounded-card elevation-12">
        <v-flex px-5 py-3 class="text-xs-center">
          <v-card-title class="justify-center">
            <h2>Register</h2>
          </v-card-title>
          <v-form ref="form" v-model="valid" lazy-validation>
            <v-text-field
              v-model="newUser.name"
              :rules="nameRules"
              prepend-inner-icon="person"
              label="Name"
              required
            ></v-text-field>

            <v-text-field
              v-model="newUser.email"
              :rules="emailRules"
              prepend-inner-icon="email"
              label="E-mail"
              placeholder="john@gmail.com"
              required
            ></v-text-field>

            <v-text-field
              v-model="newUser.phone"
              :rules="phoneRules"
              prepend-inner-icon="phone"
              label="Phone Number"
              placeholder="012-3243567"
              required
            ></v-text-field>

            <v-text-field
              v-model="newUser.password"
              :rules="passwordRules"
              prepend-inner-icon="lock"
              :append-icon="showPassword ? 'visibility' : 'visibility_off'"
              :type="showPassword ? 'text' : 'password'"
              @click:append="showPassword = !showPassword"
              label="Password"
              required
            ></v-text-field>

            <v-btn color="#B0BEC5" @click="createAccount">Create Account</v-btn>
          </v-form>
        </v-flex>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  data() {
    return {
      valid: true,
      showPassword: false,
      newUser: {
        name: "",
        email: "",
        phone: "",
        password: ""
      },
      nameRules: [
        v => !!v || "Name is required",
        v => (v && v.length >= 3) || "At least 3 characters"
      ],
      emailRules: [
        v => !!v || "E-mail is required",
        v => /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(v) || "E-mail must be valid"
      ],
      phoneRules: [
        v => !!v || "Phone number is required",
        v => (v && v.length >= 10) || "At least 10 digits"
      ],
      passwordRules: [
        v => !!v || "Password is required",
        v => (v && v.length >= 8) || "At least 8 characters"
      ]
    };
  },
  methods: {
    createAccount() {
      if (this.$refs.form.validate()) {
        this.$store.commit("addNewUser", this.newUser);
        this.$router.push("/login");
      }
    }
  },
  beforeMount() {
    this.$store.dispatch("fetchUsers");
  }
};
</script>

<style scoped>
.rounded-card {
  border-radius: 30px;
}
</style>
