<template>
  <v-flex xs12 sm6 md4>
    <v-card :href="item.url" target="_blank" color="rgba(235,235,235)" hover class="ma-2">
      <v-layout xs12>
        <v-flex xs10>
          <v-img :src="require(`.${item.logo}`)" aspect-ratio="1" contain height="70px"></v-img>
        </v-flex>
        <v-flex xs2>
          <v-btn icon flat @click.prevent="$emit('deleteChildItem')">
            <v-icon>delete</v-icon>
          </v-btn>
        </v-flex>
      </v-layout>
      <v-layout xs12>
        <v-flex xs10>
          <v-card-title primary-title>
            <h4>{{ item.title }}</h4>
          </v-card-title>
        </v-flex>
        <v-flex xs2>
          <v-card-actions>
            <v-btn href icon flat color="pink" @click.prevent="addFav">
              <v-icon :disabled="!favourite">favorite</v-icon>
            </v-btn>
          </v-card-actions>
        </v-flex>
      </v-layout>
    </v-card>
  </v-flex>
</template>

<script>
export default {
  props: ["item"],
  data() {
    return {
      favourite: false
    };
  },
  methods: {
    addFav() {
      this.favourite = !this.favourite;
      if (this.favourite) {
        localStorage.setItem(
          this.$store.getters.getLoginUser.name.concat(this.item.title),
          this.favourite
        );
      } else {
        localStorage.removeItem(
          this.$store.getters.getLoginUser.name.concat(this.item.title)
        );
      }
    },
    deleteItem() {
      this.$emit("deleteChildItem", this.$vnode.key);
    }
  },
  created() {
    this.favourite = localStorage.getItem(
      this.$store.getters.getLoginUser.name.concat(this.item.title)
    );
  }
};
</script>
