<template>
  <v-layout row wrap class="mx-5 mb-5 pb-5">
    <v-flex xs12>
      <h1 class="text-xs-left ml-2">{{ services.title }}</h1>
    </v-flex>
    <service-item
      v-for="(item, index) in disp.sublinks"
      :key="index"
      :item="item"
      v-on:deleteChildItem="deleteItem(index)"
    />

    <new-item v-show="show[0]" :item="newItems[0]" v-on:deleteNewItem="deleteNewItem(0)" />
    <new-item v-show="show[1]" :item="newItems[1]" v-on:deleteNewItem="deleteNewItem(1)" />
    <new-item v-show="show[2]" :item="newItems[2]" v-on:deleteNewItem="deleteNewItem(2)" />

    <v-flex xs12>
      <v-dialog v-model="showDialog" width="500">
        <template v-slot:activator="{ on }">
          <v-btn color="red lighten-2" dark v-on="on">Add Service</v-btn>
        </template>

        <v-card>
          <v-card-title class="headline grey lighten-2" primary-title>Add Your Own Service</v-card-title>

          <v-card-text>
            <v-layout row align-center justify-center>
              <v-flex xs2>
                <h3>Title:</h3>
              </v-flex>
              <v-flex xs8>
                <v-text-field v-model="newTitle" placeholder="e.g. My Doctor" required></v-text-field>
              </v-flex>
            </v-layout>
            <v-layout row align-center justify-center>
              <v-flex xs2>
                <h3>URL:</h3>
              </v-flex>
              <v-flex xs8>
                <v-text-field v-model="newURL" placeholder="e.g. https://www.google.com" required></v-text-field>
              </v-flex>
            </v-layout>
          </v-card-text>
          <v-divider></v-divider>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary" flat @click="showDialog = false">Cancel</v-btn>
            <v-btn color="primary" flat @click="addNewService">Add Service</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-flex>
  </v-layout>
</template>

<script>
import ServiceItem from "./ServiceItem";
import NewItem from "./NewItem";

export default {
  props: ["service"],
  components: {
    ServiceItem,
    NewItem
  },
  data() {
    return {
      services: this.service,
      showDialog: false,
      newItems: [{}, {}, {}],
      counter: 0,
      newTitle: "",
      newURL: "",
      show: [false, false, false]
    };
  },
  methods: {
    addNewService() {
      this.showDialog = false;
      let newItem = { title: this.newTitle, url: this.newURL };
      this.newItems[this.counter] = newItem;
      (this.newTitle = ""), (this.newURL = "");
      this.show[this.counter] = true;
      this.counter += 1;
    },
    deleteItem(index) {
      this.services.sublinks.splice(index, 1);
    },
    deleteNewItem(index) {
      this.show[index] = false;
    }
  },
  computed: {
    disp() {
      return this.services;
    }
  }
};
</script>
