<template>
  <v-layout row wrap mt-3 pt-3 mx-5>
    <v-layout>
      <v-flex xs12 pa-3>
        <h1>Request For Door Assistance</h1>
        <blockquote
          class="bquote"
        >If you have encountered any problems that you could not solve by yourself, do not hesistate for request for a door assistance! Start by filling out the form below and one of our representatives will contact you directly, to assist you with your requirements.</blockquote>
      </v-flex>
    </v-layout>
    <v-flex xs10 sm8 md5>
      <v-flex pa-3 class="text-xs-center">
        <h3 class="text-xs-left">Request Form</h3>
        <v-divider></v-divider>
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-text-field
            v-model="name"
            :rules="nameRules"
            prepend-inner-icon="person"
            label="Name"
            placeholder="John Smith"
            required
          ></v-text-field>

          <v-text-field
            v-model="email"
            :rules="emailRules"
            prepend-inner-icon="email"
            label="E-mail"
            placeholder="john@gmail.com"
            required
          ></v-text-field>

          <v-text-field
            v-model="phone"
            :rules="phoneRules"
            prepend-inner-icon="phone"
            label="Phone Number"
            placeholder="012-3423567"
            required
          ></v-text-field>

          <v-menu
            ref="menu"
            v-model="menu"
            :close-on-content-click="false"
            :nudge-right="40"
            :return-value.sync="date"
            lazy
            transition="scale-transition"
            offset-y
            full-width
            min-width="290px"
          >
            <template v-slot:activator="{ on }">
              <v-text-field
                v-model="date"
                label="Door Assistance Date"
                prepend-inner-icon="event"
                readonly
                v-on="on"
                required
              ></v-text-field>
            </template>
            <v-date-picker v-model="date" no-title scrollable>
              <v-spacer></v-spacer>
              <v-btn flat color="primary" @click="menu = false">Cancel</v-btn>
              <v-btn flat color="primary" @click="$refs.menu.save(date)">OK</v-btn>
            </v-date-picker>
          </v-menu>

          <v-select
            v-model="select"
            :items="category"
            :rules="categoryRules"
            label="Category"
            required
          ></v-select>

          <v-textarea v-model="message" height="80px" name="input-7-1" label="Message"></v-textarea>

          <v-checkbox v-model="checkbox" label="I am not a robot." :rules="checkboxRules" required></v-checkbox>

          <v-btn color="rgba(200, 200, 200)" @click="submitForm">Submit</v-btn>
        </v-form>
      </v-flex>
    </v-flex>
    <v-flex xs10 sm8 md5 offset-md1>
      <v-flex pa-3>
        <h3>Alternatives</h3>
        <v-divider></v-divider>
        <blockquote
          class="bquote"
        >In case your form could not reach us for any reason, you can contact us through our phone number and email stated below:</blockquote>
        <v-btn href="tel: 03-612345678">
          <h4>03-612345678</h4>
        </v-btn>
        <v-btn href="mailto: contact@folks.com">
          <h4>contact@folks.com</h4>
        </v-btn>
      </v-flex>
    </v-flex>
    <v-dialog v-model="loading" hide-overlay persistent width="300">
      <v-card color="primary" dark>
        <v-card-text>
          Sending Message...
          <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog v-model="success" hide-overlay persistent width="300">
      <v-card color="green" dark>
        <v-card-text>
          <v-icon>done</v-icon>Message successfully sent!
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
import { setTimeout } from "timers";
export default {
  data() {
    return {
      date: new Date().toISOString().substr(0, 10),
      menu: false,
      valid: true,
      name: "",
      email: "",
      phone: "",
      message: "",
      select: null,
      checkbox: false,
      category: ["General Assistance", "Technical Assistance", "Others"],
      nameRules: [
        v => !!v || "Name is required",
        v => (v && v.length >= 3) || "At least 3 characters"
      ],
      emailRules: [
        v => !!v || "E-mail is required",
        v => /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(v) || "E-mail must be valid"
      ],
      phoneRules: [
        v => !!v || "Phone number is required",
        v => (v && v.length >= 10) || "At least 10 digits"
      ],
      categoryRules: [v => !!v || "Category is required"],
      checkboxRules: [v => !!v || "Checkbox must be ticked"],
      loading: false,
      success: false
    };
  },
  watch: {
    loading(val) {
      if (!val) return;
      setTimeout(() => {
        this.loading = false;
        this.success = true;
      }, 3000);
    },
    success(val) {
      if (!val) return;
      setTimeout(() => (this.success = false), 1500);
    }
  },
  methods: {
    submitForm() {
      if (this.$refs.form.validate()) {
        this.loading = true;
      }
    }
  }
};
</script>

<style scoped>
.bquote {
  font-size: 18px;
  padding-top: 10px;
  padding-bottom: 10px;
}
</style>
